* {
  box-sizing: border-box;
}

body {
  background-color: #efefef;
  color: #444;
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 16px;
  line-height: 1.6;
  margin: 40px auto;
  max-width: 750px;
  padding: 0 10px;
}

h1, h2, h3 {
  line-height: 1.2;
}

h1 {
  text-align: center;
}

@media (max-width: 600px) {
  h1 {
    text-align: left;
  }
}

form {
  margin-bottom: 24px;
  width: 100%;
}

.searchbox {
  background-color: #fafafa;
  border: 3px solid #444;
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 24px;
  height: 48px;
  padding: 0 10px;
  width: calc(100% - 100px);
}

.submit-btn {
  background-color: transparent;
  border: none;
  font-family: 'IBM Plex Sans', sans-serif;
  font-size: 24px;
  text-decoration: underline;
  width: 100px;
}

.submit-btn:hover {
  cursor: pointer;
}

.submit-btn:focus,
.searchbox:focus {
  outline: none;
}

.artist-details {
  align-items: center;
  display: flex;
  margin-bottom: 24px;
}

@media (max-width: 600px) {
  .artist-image {
    max-width: 50%;
  }
}

.artist-details__top-level {
  margin-left: 24px;
}

a {
  color: inherit;
  text-decoration: underline;
  transition: color 150ms cubic-bezier(0.215, 0.61, 0.355, 1);
}

a:hover {
  color: #96f;
}

table {
  border-collapse: collapse;
  width: 100%;
}

thead {
  border-bottom: 1px solid #000;
}

th {
  font-size: 14px;
  letter-spacing: 1px;
  padding: 8px 10px 0px;
  text-align: left;
  text-transform: uppercase;
}

tbody tr {
  border-bottom: 1px solid #aaa;
}

tbody tr:last-of-type {
  border-bottom: none;
}

td {
  padding: 10px 10px 8px;
}

tbody tr td:last-of-type {
  min-width: 100px;
}

@media (max-width: 600px) {
  table tr th:nth-child(3),
  table tr td:nth-child(3) {
    display: none;
  }
}

